      <van-tab title="饮水"></van-tab>
      <van-tab title="食物"></van-tab>
      <van-tab title="血糖"></van-tab>
    </van-tabs> -->
  </div> 
  <div >
    <transition name="fade">
      <keep-alive>
        <router-view></router-view>
