</template>

<script>
import { onMounted, reactive, toRefs } from "vue";
import {useRouter} from "vue-router";
import {getHomeDetialHttp} from "@/views/new-home/common/serviec";
import {formatYYMMDD, formatYYMMDDHHMMSS} from "@/utils/util";

export default {
  name: "index",
  setup(props){

    const router = useRouter()

    onMounted(()=>{
      state.tabPages = {
        0: '/new-home/drink', // 饮水
        1: '/new-home/food', // 食物
        2: '/new-home/bloodsugar',  // 血糖
      }
    })

    // 请求网络数据
    const requestData = async () => {
      const res = await getHomeDetialHttp('7','2',
          formatYYMMDD(new Date()),formatYYMMDD(new Date()),0,0)
      const { code, data } = res
      console.log("血压首页数据--》",data)
      if(code === 200){
        const { object = [],
          suggestion: { foodSuggestion = "", sportSuggestion = "", commonSuggestion = "", doctorSuggestion = "" }
        } = data
        const { systolic = '0', diastolic = '0', time, pulse = '0' } = object[0]
        // 最近一次测量赋值
        state.firstCardData.rightText = time ? formatYYMMDDHHMMSS(time) : ''
        if(object.length === 0) return
        state.firstCardData.data[0].value = systolic
        state.firstCardData.data[1].value = diastolic
        state.firstCardData.data[2].value = pulse
        state.list[0].content = foodSuggestion
        state.list[1].content = sportSuggestion
        state.list[2].content = commonSuggestion
        state.list[3].content = doctorSuggestion
      }
    }


    const state = reactive({
      tabPages: []
    })

    const onClickTab = ({ name }) =>{
      console.log("tabtabtab", state.tabPages[name])
      router.push(state.tabPages[name])
    }

    return{
      ...toRefs(state),
      onClickTab
    }
  }

}
</script>

