import axios, {BASEURL} from '@/utils/axios'
import {getUserId} from "@/utils/util";
import {getIDCardNo} from "@/views/new-home/common/util";

/**
 * 当日血糖首页详情
 *    "dataType":"1",//数据类型
 "idcard":"",//身份证号
 "startDate":"",//开始日期yyyy-MM-dd 查询类型为0时查询某日的数据时startDate和endDate传一样的日期，查询类型为2时传任意时间都可，其他情况就按正常开始日期-结束日期查询
 "endDate":"",//结束日期
 "type":"",//查询类型 0-日、周、月 1-年 2-最后一次 为年的时候为分页查询
 "page":"",//分页数据 当前页 查询类型为0和2时传0
 "size":""//分页数据 每页显示数量 查询类型为0和2时传0
 */
export function getHomeDetialHttp(dataType,type,startDate,endDate,page,size) {
  return getIDCardNo() && axios.get(`hhController/data/get`,{ params: {
        dataType, idcard: getIDCardNo(), startDate, endDate, type, page,size
    }});
}


